.wrapper {
    width: 100%;
    height: auto;
    background-color: var(--backgroung-dark-gray);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 999;
}

.wrapper__content {
    width: 85%;
    height: 95%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
}

.wrapper__content__spanTitle {
    font-size: 52px;
    font-weight: 700;
    color: white;
    z-index: 999;
    margin-bottom: 30px;
}

.wrapper__content__cardsBlock__hidden {
    opacity: 0;
    width: 100%;
    overflow: hidden;
}

.wrapper__content__cardsBlock {
    position: relative;
    width: 100%;
    /* height: 83%; */
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
    animation: cardsAnimation 2s ease forwards;
    margin-bottom: 50px;
}

.wrapper__content__cardsBlock__card {
    width: 30%;
    height: 450px;
    background-color: var(--background-card-gray);
    border-radius: 15px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

/* .wrapper__content__cardsBlock__card__imgBlock {
    width: 100%;
    height: 35%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
} */

/* .wrapper__content__cardsBlock__card__imgBlock__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
} */

.wrapper__content__cardsBlock__card__textBlock {
    width: 80%;
    height: 40%;
    display: flex;
    flex-direction: column;
    z-index: 999;
}

.wrapper__content__cardsBlock__card__spanTitle {
    margin-top: 10px;
    font-size: 23px;
    font-weight: 620;
    color: var(--text-color-base);
    margin-bottom: 15px;
    z-index: 999;
}

.wrapper__content__cardsBlock__card__spanDescription {
    font-size: 15px;
    font-weight: 500;
    color: var(--text-color-base);
    z-index: 999;
}

.wrapper__content__cardsBlock__card__orderBlock {
    margin-top: 0px;
    width: 80%;
    height: 25%;
    display: flex;
    flex-direction: column;
}

.wrapper__content__cardsBlock__card__orderBlock__cost {
    font-weight: 500;
    font-size: 27px;
    color: var(--red-color);
    padding: 10px 0 10px 0;
}

.wrapper__content__cardsBlock__card__orderBlock__button {
    cursor: pointer;
    padding: 15px 0 15px 0;
    font-weight: 700;
    font-size: 16px;
    border-radius: 5px;
    border: none;
    background-color: var(--red-color);
    color: white;
    box-shadow: 0 7px 10px -5px rgba(237, 27, 36, 0.5);
    transition: 0.2s;
}

/*geeegreg*/
form {
    width: 100%;
    height: 25%;
    display: flex;
    flex-direction: column;
}



.wrapper__content__cardsBlock__card__orderBlock__button:hover {
    background-color: rgb(238, 50, 60);
}

